.CompactCard {
    display: flex;
    flex: 1;
    height: 9rem!important;
    border-radius: 0.7rem;
    color: white;
    position: relative;
    cursor: pointer;
    padding: 1rem;
    align-items: center; /* Center-aligns elements vertically */
}

.CompactCard:hover {
    box-shadow: none!important;
}

/* Radial Bar */
.radialBar {
    flex: 0 0 auto; /* Prevents the bar from stretching */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; /* Centers the radial bar */
    gap: 1rem; /* Spacing between elements inside radialBar */
    margin-left: 0.5rem; /* Adjust spacing inside the card */
    width: 120px;
    height: 120px;
}

.CircularProgressbar {
    width: 4rem!important;
    overflow: visible;
}

.CircularProgressbar-path {
    stroke: white!important;
    stroke-width: 12px!important;
    filter: drop-shadow(2px 4px 6px white);
}

.CircularProgressbar-trail {
    display: none;
}

.CircularProgressbar-text {
    fill: white!important;
}

.radialBar > span {
    font-size: 18px;
    font-weight: bold;
}

.detail {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    gap: 2rem; /* Adds spacing between elements */
}

.detail > *:first-child { 
    margin-bottom: auto; /* Moves Png upwards */
}

.detail > span:nth-child(2) {
    font-size: 22px;
    font-weight: bold;
}

.detail > span:nth-child(3) {
    font-size: 12px;
    margin-top: auto; /* Moves "Last 24 hours" downward */
}

/*Expandable Card*/

.ExpandedCard{
    position: absolute;
    width: 60%;
    height: 70%;
    z-index: 9;
    left: 13rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
}
.ExpandedCard >span:nth-of-type(1){
    color: white;
    font-size: 26px;
    font-weight: bold;
    text-shadow: 0px 0px 15px white;
}
.ExpandedCard >span:nth-of-type(2){
    color: white;
    font-size: 15px;

}
.chartContainer{
    width: 70%;
}

@media screen and (max-width: 1200px){
    .ExpandedCard{
        top: 2rem;
        height: 70vh;
        left: 6rem;
    }
}
@media screen and (max-width: 768px){
    .ExpandedCard{
        top: 8rem;
        height: 50%;
        left: 15px;
        width: 80%;
    }
}